body {
  justify-content: center;
  align-items: center;
  width: 100vh;
  height: 100vh;
  margin: 0 auto;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__circle {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: fixed;
}

.stopwatch__minutes {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin-bottom: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  position: absolute;
}

.stopwatch__minutes--speed-up {
  animation-duration: 600s;
}

.stopwatch__seconds {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  margin-bottom: 38vmin;
  animation: 60s linear infinite rotate;
  position: absolute;
}

.stopwatch__seconds--speed-up {
  animation-duration: 10s;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
